import { useState, useEffect } from 'react';
import axios from 'axios';
import StartingAutocomplete from "./pages/components/Dashboard/Autocomplete/StartingAutocomplete"
import EndingAutocomplete from "./pages/components/Dashboard/Autocomplete/EndingAutocomplete"

import { MapContainer, TileLayer, Marker, Popup, Polyline, ZoomControl } from 'react-leaflet';

const App = () => {
    const [locationAllowed, setLocationAllowed] = useState(false);
    const [startLocation, setStartLocation] = useState(null)
    const [endLocation, setEndLocation] = useState(null)
    const [map, setMap] = useState(null)
    const [mapPosition, setMapPosition] = useState([28.6704777, -81.4059541])
    const [directions, setDirections] = useState(null)
    const [price, setPrice] = useState(null)
    function reverse_coords(obj) {
        let new_array = [];
        for (var coord in obj) {
            new_array.push([obj[coord][1], obj[coord][0]]);
        }
        return new_array;
    }
    async function getDirections() {
        let apiKey = "pk.e357e7ac696520f9581388a028f88ffe"
        let coordinates = `${startLocation.coords[1]},${startLocation.coords[0]};${endLocation.coords[1]},${endLocation.coords[0]}`
        console.log(coordinates)
        let req = await axios.get(`https://us1.locationiq.com/v1/directions/driving/${coordinates}?key=${apiKey}&steps=true&geometries=geojson`).then((result, err) => {
            console.log(result)
            let coords = []
            let data = result.data.routes[0].legs[0].steps.map(element => {
                return element.geometry.coordinates.forEach(elem => coords.push(elem))
            });
            let price = result.data.routes[0].distance / 1609
            setPrice(((price.toFixed(2) * 1.08) + 4.87).toFixed(2))
            setDirections(coords)
        })
    }
    const lineColor = {
        color: 'red'
    }
    const path = reverse_coords(directions)
    const findRideStyles = {
        position: 'absolute',
        zIndex: '1000',
        width: "100%",
        backgroundColor: 'white',
        left: 0
    }
    const findRideStyles2 = {
        position: 'absolute',
        zIndex: '1000',
        width: "100%",
        backgroundColor: 'white',
        left: 0,
        bottom: 0
    }
    function success(data) {
        console.log(data)
        setMapPosition([data.coords.latitude, data.coords.longitude])
    }
    useEffect(() => {
        if (locationAllowed) {
            navigator.geolocation.watchPosition(success)
        }
    }, [locationAllowed])
    return (
        <>
            <div style={findRideStyles} className="p-2">
                <div className="col-sm-10 mb-2">
                    <StartingAutocomplete setLocationAllowed={setLocationAllowed} setStartLocation={setStartLocation}></StartingAutocomplete>
                    <br />
                    <EndingAutocomplete setEndLocation={setEndLocation}></EndingAutocomplete>
                </div>
                <div className="col-sm-2">
                    <button style={{display: 'block', marginLeft: 'auto'}} className="btn btn-primary" onClick={() => getDirections()}>Find A Ride</button>       
                </div>
            </div>
            <MapContainer zoomControl={false} style={{ height: '100vh' }} center={mapPosition} zoom={16} scrollWheelZoom={false} whenCreated={setMap}>
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                <Marker position={mapPosition}>
                    <Popup>
                        A pretty CSS3 popup. <br /> Easily customizable.
                    </Popup>
                </Marker>
                <Polyline pathOptions={lineColor} positions={path} />
                <ZoomControl position="bottomright" />
            </MapContainer>
            {
                price &&
                <div style={findRideStyles2} className="p-2">
                    <div className="col-sm-8">
                        <h1 className="text-center">Trip Details</h1>
                        <hr/>
                        <p><strong>iGotchu Fees:</strong> $4.87</p>
                        <p><strong>Fare Price:</strong> ${price - 4.87}</p>
                        <p><strong>Estimated Price: </strong>${price}</p>
                    </div>
                    <div className="col-sm-4">
                        <button className="btn btn-primary" style={{display: 'block', marginLeft: 'auto'}}>Book Ride</button>
                    </div>
                </div>
            }
        </>
    )
}

export default App;